import { ENDPOINTS, API_BASE_URL } from '../constant.js'
import userRepository from './users'
export default {
    /**
    * contact api to log user session
    * @param {email_or_username, password, grant_type, user_agent} p 
    */
    async login(p) {
        await fetch(API_BASE_URL + ENDPOINTS.login, {
            method: "POST",
            body: JSON.stringify(p),
            headers: { "Content-Type": "application/json" }
        }).then(resp => resp.json().then(async res => {
            dataRepository.account = { ...res }
            sessionStorage.setItem('token', res.access_token)
            const img = await userRepository.retrieveContactPicture(res.user.id)
            if (img.base64) {
                res.user.profile_picture = img.base64
            }
            sessionStorage.setItem('currentUser', JSON.stringify(res.user))
        }));
    },
    /**
     * get user token from session 
     */
    async retrieveToken() {
        return await sessionStorage.getItem('token')
    }
}

/**
 * Data repository containing shared mixin data for component
 */
export let dataRepository = {
    account: {}
}
