<template>
<div>
    <router-link to="/keychain"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
        <div class="layout-body">
            <div class="jumbo">
                <p class="container-title"><b>Edit your profile</b></p>
            </div>
            <div class="rounded">
             <picture-input 
      ref="pictureInput"
      width="124" 
      radius="2"
      height="124" 
      margin="16" 
      :hideChangeButton="true"
      accept="image/jpeg,image/png, image/jpg" 
      size="0.2" 
      :prefill="'data:image/png;base64,'+ user.profile_picture"
      :prefillOptions="{mediaType: 'image/png'}"
      button-class="btn"
      :custom-strings="{
        upload: '<h1>Bummer!</h1>',
        drag: 'Drag a file or click here to change profile picture'
      }"
      @change="updateProfilePicture">
    </picture-input>
            </div>

            <div class="section-form">
                <div>
                    <div class="form-group">

                        <form class="form-group">
                            <div class="row">
                                <div class="col">
                                    <label >First name *</label>
                                    <input v-model="user.first_name" type="text" class=" havr-black-input  form-control ">
    </div>
                                    <div class="col">
                                        <label >Last name *</label>
                                        <input v-model="user.last_name" type="text" class=" havr-black-input form-control ">
    </div>
                                    </div>
                        </form>
                        <div class="form-group">
                            <label>Username *</label>
                            <input v-model="user.username" class="form-control havr-black-input">
  </div>
                            <div class="form-group">
                                <label>Email adress *</label>
                                <input v-model="user.email" class="form-control havr-black-input">
  </div>
                            </div>
                            <button @click="updateProfil" id="formButton"  class="btn btn-primary havr-black-color" >Save</button>
                        </div>

                        <p @click="showPasswordEditModal = true, password = null" class="change-password">Change password <i class="fa fa-chevron-right align-center-icn" aria-hidden="true"></i></p>

                    </div>
                </div>
                <b-modal :hide-footer="true" :ok-title="'Save'" :hide-header="true" :ok-variant="'havr-black-color'" centered v-model="showPasswordEditModal">
                    <div class="form-group">
                        <label>Type in your current password *</label>
                        <input v-model="password" type="password"  class="form-control havr-black-input">
  </div>
                        <div class="form-group">
                            <label for="exampleInputEmail1">Type in your new password *</label>
                            <input v-model="newPassword" type="password" class="form-control havr-black-input">
                            <small class="form-text text-muted">Min 8 characters, 1 Uppercase letter, 1 number and 1 special character</small>
                        </div>
                        <div class="form-group">
                            <label>Confirm your new password *</label>
                            <input v-model="confirmNewPassword" type="password" class="form-control havr-black-input">
  </div>
   <button @click="updatePassword" class="btn btn-primary havr-black-color modal-button" >Save</button>
                                <button @click="cancelPasswordValidation" class="btn btn-primary havr-black-inversed" >Cancel</button>

              
                </b-modal>

                <b-modal :hide-footer="true" :ok-title="'Save'" :hide-header="true" :ok-variant="'havr-black-color'" centered v-model="showPasswordModal">
                    <div class="form-group">
                        <label>Please enter your password to save your modifications</label>
                        <input v-model="password" type="password" class="form-control havr-black-input">
  </div>
   <button @click="updateEmail" class="btn btn-primary havr-black-color modal-button" >Save</button>
                            <button @click="showPasswordModal = !showPasswordModal" class="btn btn-primary havr-black-inversed" >Cancel</button>
              
                </b-modal>
</div>
</template>

<script>
import bModal from "bootstrap-vue/es/components/modal/modal";
import bModalDirective from "bootstrap-vue/es/directives/modal/modal";
import repository from "@/repository/users";
import PictureInput from "vue-picture-input";
import notifiy from "vue-notification";
export default {
  components: {
    "b-modal": bModal,
    PictureInput
  },
  directives: {
    "b-modal": bModalDirective
  },
  data() {
    return {
      user: JSON.parse(sessionStorage.getItem("currentUser")),
      originalEmail: JSON.parse(sessionStorage.getItem("currentUser")).email,
      originalProfil: JSON.parse(sessionStorage.getItem("currentUser")),
      usernameRegex: /^[a-zA-Z0-9]+$/,
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m,
      showPasswordModal: false,
      showPasswordEditModal: false,
      showUpdatePicture: null,
      newPassword: null,
      newProfilePicture: null,
      confirmNewPassword: null,
      password: null,
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  methods: {
    async updateProfil() {
      if (this.newProfilePicture !== null) {
        await repository.updateProfilePicture({
          extension: "jpeg",
          base64_picture: this.newProfilePicture.substring(
            this.newProfilePicture.indexOf(",") + 1
          )
        });
        this.notifySuccess("Profil picture updated with success !", 2000);
        this.user.profile_picture = this.newProfilePicture.substring(
          this.newProfilePicture.indexOf(",") + 1
        );
        sessionStorage.setItem("currentUser", JSON.stringify(this.user));
      }
      const userUpdate = {
        username: this.user.username.trim(),
        last_name: this.user.last_name.trim(),
        first_name: this.user.first_name.trim()
      };
      if (
        userUpdate.username !== this.originalProfil.username ||
        userUpdate.last_name !== this.originalProfil.last_name ||
        userUpdate.first_name !== this.originalProfil.first_name ||
        this.user.email !== this.originalEmail
      ) {
        if (!this.checkInputs(userUpdate))
          return this.notifyError("Error : All fields are mandatory");
        if (this.checkEmail(this.user.email) === false)
          return this.notifyError("Error : Bad email format");
        if (this.originalEmail !== this.user.email) {
          return this.showPasswordValidationModal();
        } else this.saveProfil(userUpdate);
      }
    },
    async saveProfil(userUpdate) {
      if (this.checkUsername(userUpdate.username) === false)
        return this.notifyError("Error : Bad username format");
      const res = await repository.updateProfil(userUpdate, this.user.id);
      if (!res) this.notifyError("Error : Internal Error");
      else {
        this.notifySuccess("Profil updated with success !", 2000);
        sessionStorage.setItem("currentUser", JSON.stringify(this.user));
        this.user = JSON.parse(sessionStorage.getItem("currentUser"))
      }
    },
    showPasswordValidationModal() {
      this.showPasswordModal = true;
    },
    async updateEmail() {
      const emailUpdate = { email: this.user.email, password: this.password };
      const res = await repository.updateEmail(emailUpdate);
      if (res) {
        this.notifySuccess(
          "We sent you an email to " +
            this.user.email +
            ". Please click on the link inside the email to confirm your new email adress",
          5000
        );
        this.showPasswordModal = false;
      } else this.notifyError("Error : incorrect password");
      this.saveProfil();
    },
    updateProfilePicture(picture) {
      if (picture) {
        this.newProfilePicture = picture;
      } else
        this.notifyError(
          "Error: Allowed extensions are: `png`, `jpeg` and jpg The file size must be under 200 KB"
        );
    },
    async updatePassword() {
      if (this.checkPasswordValidation(this.newPassword) === false) {
        return this.notifyError(
          "Error: Password must be minimum 8 characters, 1 Uppercase letter, 1 number and 1 special character."
        );
      }
      if (this.newPassword !== this.confirmNewPassword)
        return this.notifyError("Error: Password confirmation dont match.");
      const password = {
        new_password: this.newPassword,
        current_password: this.password
      };
      const res = await repository.updatePassword(password);
      if (res) {
        this.showPasswordEditModal = false;
        this.refreshPasswordEditForm();
        this.notifySuccess("Password updated with success", 2000);
      } else this.notifyError("Error: incorrect password");
    },
    cancelPasswordValidation() {
      this.showPasswordEditModal = !this.showPasswordEditModal;
      this.refreshPasswordEditForm();
    },
    refreshPasswordEditForm() {
      this.password = null;
      this.newPassword = null;
      this.confirmNewPassword = null;
    },
    checkInputs(p) {
      for (const i in p) {
        if (p[i].trim() === "") return false;
      }
      return true;
    },
    notifySuccess(msg, time) {
      return this.$notify({
        message: msg,
        type: "success",
        timeout: time,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    checkEmail(p) {
      return RegExp(this.emailRegex).test(p);
    },
    checkUsername(p) {
      return RegExp(this.usernameRegex).test(p);
    },
    checkPasswordValidation(p) {
      return RegExp(this.passwordRegex).test(p);
    },
    notifyError(p) {
      return this.$notify({
        message: p,
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    }
  }
};
</script>

